import socket from "~/plugins/socket.io.js";

export default {
  name: "ChatForm",
  props: ["user"],
  data() {
    return {
      msg: "",
    };
  },
  methods: {
    sendSms() {
      socket.emit("sendSms", { text: this.msg, file: null, user: this.user });
      this.msg = "";
    },
  },
};
