import socket from "~/plugins/socket.io.js";

export default {
  name: "ChatContent",
  props: ["user"],
  data() {
    return {
      messages: [],
    };
  },
  beforeMount() {
    socket.on("updateMsg", (messages) => (this.messages = messages));
  },
};
